* {padding:0; margin:0; box-sizing:border-box;}
.header {width:100%; display:flex; background:black; color:#fff; padding:20px;}
.wrap {padding:20px;}
.list {padding:20px; border-top:1px solid black;}
.list h4 {padding-bottom:10px; color:#666; display:flex; align-items:center;}
.list span {padding:5px 10px; margin:0 10px; font-size:10px; border:1px solid #666; border-radius:5px; color:#666; display:inline-block; cursor:pointer;}
button {padding:5px 10px; margin-bottom:10px;}
.modal {padding:20px; margin-top:20px; background:#eee; text-align:left;}
